<template>
    <div>
        <div id="photo"><img :src="userInfo.touxiang" @click.stop="updateimg" class="ohuo"/></div>
        <input type="file" ref="one" accept="image/*" @change="shangchuan" class="hiddenInput"/>
        <button @click="seen"></button>
        <img :src="url" alt="">
    </div>
